.banner {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-size: cover;
  background-position: center;
  /* overflow: hidden; */
  transition: 0.5s;
  filter: contrast(110%) brightness(110%);
}

.banner::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
}

.content {
  position: relative;
  padding-left: 6rem;
}

.carousel-movie-img {
  min-width: 275px;
  max-width: 275px;
  min-height: 375px;
  max-height: 375px;
  border: 3px solid white;
  border-radius: 15px;
  margin-top: 1rem;
}

.banner-title {
  margin: 10px 50px 0 0;
  background: var(--primary);
  font-weight: 600;
  color: white;
  font-style: italic;
  display: inline-block;
  padding: 5px;
  border-radius: 3px;
  overflow: hidden;
}

.banner .content h4 {
  color: white;
  font-size: 30px;
  margin-top: 0;
}

.banner .content h4 span {
  line-height: 50px;
  padding: 0 10px;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
}

.banner .content h4 span:first-child {
  padding-left: 0;
}

.banner .content h4 span i {
  background-color: var(--primary);
  color: white;
  padding: 0 8px;
  /* display: inline-block; */
  border-radius: 2px;
}

.banner .content p {
  font-size: 1.25rem;
  font-weight: 300;
  line-height: 1.5rem;
  color: white;
  margin: 10px 15px 10px 0;
  overflow: hidden;
}

.slick-dots {
  display: block;
  transform: translateY(-3.5rem);
}

.slick-dots li button:before {
  color: white !important;
  font-size: 20px !important;
  opacity: .50;
}

.slick-dots li.slick-active button:before {
  color: var(--primary) !important;
}

.slick-arrow.slick-prev.slick-disabled::before {
  opacity: 0 !important;
}

.slick-arrow.slick-prev::before {
  font-size: 45px !important;
  color: var(--primary) !important;
  opacity: 1 !important;
  padding: 7px 3px 0 3px;
  background-color: white;
  border-radius: 50%;
}

.slick-arrow.slick-next::after {
  opacity: 1 !important;
}

.slick-arrow.slick-next::before {
  font-size: 45px !important;
  color: var(--primary) !important;
  opacity: 1 !important;
  padding: 7px 3px 0 3px;
  background-color: white;
  border-radius: 50%;
}

.slick-prev {
  left: 1% !important;
  z-index: 1;
}

.slick-next {
  right: 4% !important;
  z-index: 1;
}