.card-wrapper {
    position: relative;
    min-width: 220px;
    max-width: 220px;
    margin: 1rem 0.45rem;
    border-radius: 1rem;
}

.card-wrapper::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.25);
}

.cardItem {
    width: 100%;
    border-radius: 1rem;
    overflow: hidden;
    border: 2px solid white;
    font-size: 1.125rem;
}

.cardImg {
    width: 100%;
    height: 20rem;
    transition: all 0.5s ease;
}

.cardItem:hover {
    .cardImg {
        transform: scale(1.1);
    }

    border: 2px solid var(--primary);
}

.cardBody {
    border-top: 2px solid white;
    padding: 0.5rem;
    backdrop-filter: blur(70px);
}

.cardBody .cardTitle {
    height: 3.25rem;
}

.cardBody hr {
    margin: 0;
}

.cardBody li {
    margin-top: 5px;
    /* background: var(--primary);
    border-radius: 3px;
    padding: 0 1px; */
}

.cardBody b {
    /* color: var(--primary); */
    padding: 2px;
    border-radius: 3px;
    line-height: 1rem;
}

.cardBody h5 {
    display: -webkit-box;
    line-height: 1.40rem;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-size: 1.155rem;
    overflow: hidden;
    text-overflow: ellipsis;
    color: white;
    font-weight: 600;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.cardInfo {
    min-height: 110px;
    max-height: 110px;
    justify-content: space-between;
    align-items: center;
    color: white;
}

.cardInfo.compact {
    min-height: max-content;
    max-height: max-content;
}