@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200&family=Roboto:wght@300;400;500&display=swap');

* {
  margin: 0;
  padding: 0;
}

img {
  will-change: transform;
  image-rendering: auto;

}

html {
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

:root {
  --primary: #ff3700;
  --light-dark-color: #292929;
}

::-webkit-scrollbar {
  width: 12px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--primary);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000;
}

body {
  background: #000;
  font-family: 'Poppins', 'san-serif';
}

hr {
  color: white;
  border-width: 3px;
}

.heading {
  color: white;
  letter-spacing: -1px;
  font-size: 3rem;
  font-weight: 500;
  margin: 3rem 3rem 2rem 3rem;
  padding: 0.5rem;
  border-left: 0.5rem solid var(--primary);
  padding-left: 0.5rem;
  border-radius: 0.5rem;
}

.heading span{
  color: var(--primary);
}